/*
 * Inputs
 * Skin - Magento Backend
 */

$namespace: 'm2-' !default;
@import '../../../components/cs-input/src/cs-input';

.#{$namespace}input {
    $root: &;

    position: relative;

    &__label {
        color: #303030;
        font-size: 1.4rem;
        font-weight: 600;
    }

    &__input {
        line-height: 1.36;
        font-size: 1.4rem;
        padding: 0.6rem 1rem 0.6rem;
        border: 1px solid #adadad;
        color: #303030;

        &:hover:not(:disabled):not(:focus) {
            border-color: #878787;
        }

        &:focus {
            border-color: #007bdb;
        }

        &:disabled {
            background-color: #e9e9e9;
            border-color: #adadad;
            color: #303030;
        }
    }

    &__textarea {
        min-width: 4rem;
    }

    &__select {
        min-width: 8.5rem;
        padding-bottom: 0.5rem;
        padding-right: 4.4rem;
        padding-top: 0.5rem;
        background-image: url('images/dropdown-arrows-bg.svg'), linear-gradient(#e3e3e3, #e3e3e3), linear-gradient(#adadad, #adadad);
        background-position: calc(100% - 12px) -34px, 100%, calc(100% - 3.2rem) 0;
        background-size: auto, 3.2rem 100%, 1px 100%;
    }

    &--type-inline {
        #{$root}__label {
            width: 25%;
            padding-right: 30px;
        }

        #{$root}__input {
            width: 50%;
        }

        #{$root}__select {
            width: auto;
            min-width: 8.5rem;
        }
    }

    &--type-checkbox, &--type-radio {

        #{$root}__label {
            padding: 0 0 0 2.6rem;
            line-height: 1.36rem;

            &:before {
                width: 1.6rem;
                height: 1.6rem;
                top: 50%;
                transform: translateY(-50%);
                border: 1px solid #adadad;
                line-height: inherit;
                text-indent: 0;
            }
        }

        #{$root}__checkbox, #{$root}__radio {
            &:checked + #{$root}__label:before {
                content: '\e62d';
                color: #514943;
                font-size: 1.1rem;
                line-height: 125%;
            }
        }
    }

    &--type-required {
        #{$root}__label:after {
            color: #eb5202;
            font-weight: 500;
            font-size: 1.6rem;
            line-height: 1;
            margin-left: 10px;
            vertical-align: inherit;
        }
    }

    + #{$root}:not(#{$root}--type-checkbox):not(#{$root}--type-radio) {
        margin: 3rem 0 0;
    }
}
